.side-drawer {
  height: 100%;
  background: lightblue;
  box-shadow: 2px 0px 5px rgba(145, 28, 78, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 400px;
  z-index: 200;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
}

.side-drawer.open {
  transform: translateX(0);
}
.side-drawer ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.side-drawer li {
  margin: 0.5rem 0;
}
.side-drawer a {
  color: tomato;
  text-decoration: none;
  font-size: 1.2rem;
}

.side-drawer a:active,
.side-drawer a:hover {
  color: blue;
}
